import ElementColumn from './element-column';

export default class AddressColumn extends ElementColumn {
  exportValue(feature, {useDisplayValue = false}) {
    const value = this.valueFrom(feature);

    if (value) {
      if (this.part) {
        return value.address.toJSON()[this.part];
      }

      return value.address.searchableValue;
    }

    return null;
  }
}
